<template>
  <div class="page-container">
    <div class="tab-container">
      <ul class="tab-list">
        <router-link tag="li" active-class="active-tab" :to="{ path: '/show/public'}">
          <span>已发布</span>
        </router-link>
        <router-link tag="li" active-class="active-tab" :to="{ path: '/show/approval'}">
          <span>审核任务</span>
        </router-link>
      </ul>
      <i class="corner-patch" :class="{'active-corner':$route.path.includes('/show/approval')}"></i>
      <i v-if="approval_num" class="alert-dot primary-background">{{approval_num}}</i>
    </div>
    <div class="page-body">
      <router-view/>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'

export default {
  computed: mapState(['approval_num'])
}
</script>
<style lang='stylus' scoped>
  .page-container
    width 100%
    height 100%
    padding 10px
    display flex
    flex-direction column
  .tab-container
    height 32px
    width 300px
    display flex
    position relative
  .tab-list
    width 100%
    height 42px
    display flex
    flex-direction row
    color #2B3242
    font-size 12px
    background #DEE1E6
    border-top-left-radius 8px
    border-top-right-radius 8px
    overflow hidden
    & > li
      height 32px
      flex 1
      display flex
      justify-content center
      align-items center
      cursor pointer
      width 68px
      border-radius 8px
      background #DEE1E6
      position relative
      z-index 4
      &.active-tab
        background white
        border-top-left-radius 8px
        border-top-right-radius 8px
        position relative
        z-index 2
        &:before
          content: ''
          position absolute
          left -10px
          right -10px
          bottom -10px
          height 20px
          background white
  .corner-patch
    width 8px
    height 8px
    background #DEE1E6
    position absolute
    bottom 0
    right -8px
    &.active-corner
      background white
    &:before
      content ''
      position absolute
      width 8px
      height 8px
      background #e8ecf0
      border-bottom-left-radius 8px
  .cut-off-line
    display none
    width 1px
    height 20px
    background #A9AEB2
    position absolute
    top 0
    bottom 0
    margin auto 0
    &.left-line
      display block
      left 0
    &.right-line
      display block
      right 0
  .page-body
    flex 1
    overflow hidden
    background white
    border-radius 8px
    position relative
    z-index 6
  .alert-dot
    width 20px
    height 20px
    color white
    border-radius 50%
    position absolute
    top -7px
    right -8px
    z-index 12
    font-size 12px
    font-style unset
    display flex
    justify-content center
    align-items center
</style>



